          v-observe-visibility="
            (isVisible, entry) =>
              visibilityChanged(isVisible, entry, section.content)
          "
        ></div>
        <!-- PARAGRAPH -->
        <div class="section" v-if="section.type === 'paragraph'">
          <h2 class="offset-lg-3 col-lg-6" v-html="section.title"></h2>
          <p class="offset-lg-3 col-lg-6" v-html="section.text"></p>
        </div>
        <!-- TITLE SECTION -->
        <div class="section title" v-if="section.type === 'title'">
          <span class="offset-lg-3 col-lg-6" v-html="section.text"></span>
